import React from 'react';
import './SaleItem.css'
import LinkTemplate from "./LinkTemplate";

const SaleItem = ({data}) => {
    return (
        <div className='sale-item-container'>
            <div className={'sale-item-image-container'}>
                <LinkTemplate path={`/product/${data.product_id}`}>
                    <div className='sale-item-image' style={{backgroundImage: `url(${data.url_image_main})`}}>
                        <div className='sale-item-innerText'>
                            Hello
                        </div>
                    </div>
                </LinkTemplate>
            </div>
            <div className='sale-item-detail'>
                <LinkTemplate path={`/product/${data.product_id}`}>
                    <div className='sale-item-name'>{data.name}</div>
                </LinkTemplate>
                <div className={'sale-item-price-container'}>
                    <div>
                        <div className='sale-item-price'>
                            {data.price !== data.sale_price ? `${Number(data.price).toLocaleString('en')}đ` : ''}
                        </div>
                        <div
                            className='sale-item-sale_price'>{data.sale_price === -1 ? `Liên hệ` : `${Number(data.sale_price).toLocaleString('en')}đ`}</div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SaleItem;