import React, {useState} from "react";
import "./SearchHint.css";
import {connect} from "react-redux";
import SaleItem from "../../../containers/template/SaleItem";
import LinkTemplate from "../../../containers/template/LinkTemplate";

function SearchHint({query, searchData, showHint}) {
    const [isHover, setIsHover] = useState(false);
    console.log(searchData)
    return (
        <div
            onMouseOver={() => setIsHover(true)}
            onMouseLeave={() => setIsHover(false)}
            className="hint-container"
            style={{
                display: query && (isHover || showHint) ? "block" : "none",
            }}
        >
            <div className="hint-list-container">
                {searchData.isLoading ? (
                    <div className="hint-loading-container">
                        <div className="hint-loading"></div>
                    </div>
                ) : searchData.products.length === 0 ? (
                    <div
                        style={{
                            textAlign: "center",
                            fontSize: "0.9em",
                            padding: "1em"
                        }}
                    >
                        Không có kết quả
                    </div>
                ) : (
                    <div className='hint-result-container'>
                        <div className='hint-result-element'>
                            {searchData.products.map((item, index) => {
                                return <div className='hint-result-element-1'>
                                    <SaleItem data={item}/>
                                </div>
                            })}
                        </div>
                        <LinkTemplate path={`/search?query=${query}&page=1`}>
                            <div className='hint-more'>
                                Xem thêm
                            </div>
                        </LinkTemplate>
                    </div>
                )}
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    searchData: state.searchProductReducer,
});
export default connect(mapStateToProps)(SearchHint);
