import {Route, Redirect, Switch} from "react-router-dom";
import Loadable from "react-loadable";
import React from "react";
import Loading from "../loadable-loading/Loading";

const LoadableNotFound = Loadable({
    loader: () => import("../notfound/NotFound"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
});
const LoadableHome = Loadable({
    loader: () => import("../home/Home"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
});
const LoadableProduct = Loadable({
    loader: () => import("../product/Product"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
});
const LoadableSearchProduct = Loadable({
    loader: () => import("../search-product/SearchProduct"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
});
const LoadableCart = Loadable({
    loader: () => import("../cart/Cart"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
});
const LoadableParent = Loadable({
    loader: () => import("../categories/ParentCategory"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
});
const LoadableChild = Loadable({
    loader: () => import("../categories/ChildCategory"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
});

const LoadableNewsSingle = Loadable({
    loader: () => import("../News/SingleNews/SingleNews"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
});

const LoadableNewsAll = Loadable({
    loader: () => import("../News/AllNews/AllNews"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
});

const LoadableCheckout = Loadable({
    loader: () => import("../checkout/Checkout"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
})

const LoadableContact = Loadable({
    loader: () => import("../Contact/Contact"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
})

const LoadableHotCategory = Loadable({
    loader: () => import("../categories/HotCategory"),
    loading: Loading,
    timeout: 20000,
    delay: 500,
})

function Routes() {
    return (
        <Switch>
            <Route exact path="/" children={<LoadableHome/>}/>
            <Route path="/product/:id" children={<LoadableProduct/>}/>

            <Route path="/search" children={<LoadableSearchProduct/>}/>
            <Route path="/cart" children={<LoadableCart/>}/> )
            <Route path={`/p/:tag`} children={<LoadableParent/>}/>
            <Route path={`/c/:tag`} children={<LoadableChild/>}/>
            <Route path={`/tin-tuc/:news_tag`} children={<LoadableNewsSingle/>}/>
            <Route exact path={`/tin-tuc`} children={<LoadableNewsAll/>}/>
            <Route exact path={`/ho-tro`} children={<LoadableNewsAll/>}/>
            <Route path={`/ho-tro/:news_tag`} children={<LoadableNewsSingle/>}/>
            <Route exact path={`/gioi-thieu`} children={<LoadableNewsAll/>}/>
            <Route path={`/gioi-thieu/:news_tag`} children={<LoadableNewsSingle/>}/>
            <Route path={`/thanh-toan`} children={<LoadableCheckout/>}/>
            <Route path={`/lien-he`} children={<LoadableContact/>}/>
            <Route path={`/khuyen-mai`} children={<LoadableHotCategory/>}/>
            <Route children={<LoadableNotFound/>}/>
        </Switch>
    );
}

export default Routes;
