import React from 'react';
import ReactHtmlParser from 'react-html-parser'
import '../../../../../assets/css/content-style.css'
import './SaleProgram.css'
const SaleProgram = ({data}) => {
    return (
        <div className="ck-content addedClass">
            {ReactHtmlParser(data)}
        </div>
    );
};

export default SaleProgram;