import React from "react";
import "./Start.css";
import {useSelector} from "react-redux";
import LinkTemplate from "../../../containers/template/LinkTemplate";

export default function Start() {
    const location = useSelector(state => state.prefetchDataReducer.locations)
    return (
        <div className="Start-detail-container">
            <div className="Start-common-container">
                <div className="Start-common-title">THÔNG TIN CHUNG</div>
                <LinkTemplate path='/gioi-thieu/gioi-thieu-ve-cong-ty'>
                    <div className="Start-common-item">Giới thiệu về công ty</div>
                </LinkTemplate>
                <LinkTemplate path='/lien-he'>
                    <div className="Start-common-item">Liên hệ với công ty</div>
                </LinkTemplate>
            </div>
            <div className="Start-support-container">
                <div className="Start-support-title">HỖ TRỢ</div>
                <LinkTemplate path='/ho-tro/huong-dan-mua-hang'>
                    <div className="Start-support-item">Hướng dẫn mua hàng</div>
                </LinkTemplate>
                <LinkTemplate path='/ho-tro/chinh-sach-ban-hang'>
                    <div className="Start-support-item">Chính sách bán hàng</div>
                </LinkTemplate>
                <LinkTemplate path='/ho-tro/chinh-sach-thanh-toan'>
                    <div className="Start-support-item">Chính sách thanh toán</div>
                </LinkTemplate>
                <LinkTemplate path='/ho-tro/chinh-sach-van-chuyen'>
                    <div className="Start-support-item">Chính sách vận chuyển</div>
                </LinkTemplate>
                <LinkTemplate path='/ho-tro/chinh-sach-bao-hanh'>
                    <div className="Start-support-item">Chính sách bảo hành</div>
                </LinkTemplate>
                <LinkTemplate path='/ho-tro/chinh-sach-doi-tra-hang-va-hoan-tien'>
                    <div className="Start-support-item">
                        Chính sách đổi trả hàng và hoàn tiền
                    </div>
                </LinkTemplate>
                <LinkTemplate path='/ho-tro/chinh-sach-an-toan-va-bao-mat'>
                    <div className="Start-support-item">Chính sách an toàn và bảo mật</div>
                </LinkTemplate>
            </div>
            <div className="Start-locations-container">
                <div className="Start-locations-title">HỆ THỐNG CỬA HÀNG</div>
                <ul className="Start-locations-items">
                    {location.list_branch.map((item, index) =>
                        <li className="Start-locations-item" key={index}>
                            <div className="Start-locations-item-name">
                                {item.name}
                            </div>
                            <div className="Start-locations-item-phone">
                                {item.address}
                            </div>
                            <div className="Start-locations-item-phone">
                                {item.phone}

                            </div>
                        </li>)
                    }
                </ul>
            </div>
            <div className="Start-fanpage-container">
                <div className="fb-page" data-href="https://www.facebook.com/donghoplaza" data-tabs="" data-width=""
                     data-height="" data-small-header="false" data-adapt-container-width="true" data-hide-cover="false"
                     data-show-facepile="true">
                    <blockquote cite="https://www.facebook.com/donghoplaza" className="fb-xfbml-parse-ignore"><a
                        href="https://www.facebook.com/donghoplaza">Đồng Hồ Plaza</a></blockquote>
                </div>
            </div>
        </div>
    );
}
