import React from 'react';
import SkeletonTemplate from "../template/Skeleton/SkeletonTemplate";

const DetailHeadSkeleton = () => {
    return (
        <section className={"detail-head-container specific"}>
            <h1 className={"detail-head-title"}>
                <SkeletonTemplate width="100%" height="1.5em" animation={true}/>
            </h1>
            <div className="detail-head-remain">
                <SkeletonTemplate width="100%" height="2em" animation={true}/>
            </div>
            <section className={"detail-head-price"}>
                <SkeletonTemplate width="100%" height="4em" animation={true}/>
            </section>
            <hr className={"detail-head-divider"}/>
            <SkeletonTemplate width="100%" height="7em" animation={true}/>
            <hr className={"detail-head-divider"} style={{marginTop: "1rem"}}/>
            <section>
                <SkeletonTemplate width="100%" height="8em" animation={true}/>
            </section>
            <div className={"detail-head-phone"}>
                <SkeletonTemplate width="100%" height="2em" animation={true}/>
            </div>
        </section>
    );
};

export default DetailHeadSkeleton;