import React, {useState, useRef, useEffect} from "react";
import "./Navigation.css";
import NavigationList from "./NavigationList";
import {useHistory, useLocation} from "react-router-dom";
import {getItems} from "../../../api/api";
import {useSelector} from "react-redux";

function Navigation() {
    const location = useLocation();
    const categoryList = useSelector(state =>state.prefetchDataReducer.navigation)
    const history = useHistory();

    return (
        <ul className="navigation-nav-container">
            {categoryList.map((item, index) => (
                <li key={index}>
                    <div className="navigation-nav">
                        <div
                            className="navigation-title-container"
                            onClick={() => {
                                    if (location.pathname !== `/p/${item.path}`)
                                        history.push(`/p/${item.path}`);
                                }
                            }
                        >
                            <div className="navigation-title">{item.name}</div>
                            {
                                item.list_items.length === 0? null :
                                <i
                                    className="fas fa-angle-right"
                                ></i>
                            }
                        </div>
                        <NavigationList list={item.list_items}/>
                    </div>
                </li>
            ))}
        </ul>
    );
}

export default Navigation;
