import React from 'react';
import Dialog from "../../containers/template/Dialog";
import {useSelector, useDispatch} from "react-redux";
import {closeCartDialog, closeProductDialog} from "../../redux/actions/actions";
import SuccessIcon from "../../assets/success.svg"
import './CartDialog.css'
import {useHistory} from 'react-router-dom'

const CartDialog = () => {
    const history = useHistory()
    const dispatch = useDispatch()
    const state = useSelector(state => state.dialogReducer.cartDialog)
    console.log(state)
    return (
        <Dialog open={state.isOpen} onClose={() => dispatch(closeCartDialog())}>
            <div className='cart-dialog-container'>
                <img src={SuccessIcon} alt={'success'} className='cart-dialog-image'/>
                <p className='cart-dialog-text'>Sản phẩm đã được thêm vào giỏ hàng</p>
                <div className='cart-dialog-button-container'>
                    <button className='cart-dialog-button btn-1' onClick={() => {
                        dispatch(closeCartDialog())
                    }}>Xem tiếp sản phẩm</button>
                    <button className='cart-dialog-button btn-2' onClick={()=>{
                        dispatch(closeCartDialog())
                        dispatch(closeProductDialog())
                        history.push('/thanh-toan')
                    }}>
                            Đi tới thanh toán
                    </button>
                </div>
            </div>
        </Dialog>
);
};

export default CartDialog;