import React, {useState} from "react";
import "./DetailImage.css";
import {SlideTemplate} from "../../../template/slide-template/SlideTemplate";

const DetailImage = ({data, changeImageMain}) => {
    const [currentPost, setCurrentPost] = useState({
        index: 0,
    })
    const notifyImageChange = (e, index) => {
        setCurrentPost({
            ...currentPost,
            index: index
        })
        changeImageMain(data[index])
    }
    const list_image = data.map((item, index) => {
        return (
            <div key={index} style={{padding: '5px', width: '100%'}}>
                <div className='detail-image-element'
                     style={{
                         backgroundImage: `url(${item})`,
                         border: currentPost.index === index ? '2px solid #2196f3' : 'none'
                     }}
                     onClick={e => {
                         notifyImageChange(e, index)
                     }}>
                    <p className={'inner-text'}>Image main</p>
                </div>
            </div>
        )
    })
    return <div className={"detail-image-container"}>
        <SlideTemplate data={list_image} isFixed={true} number_of_item={5}></SlideTemplate>
    </div>;
};

export default DetailImage;
