import React, {useEffect, useState} from 'react';
import LinkTemplate from "../template/LinkTemplate";
import './NavigationElement.css'
import {useDispatch} from "react-redux";
import {closeHomeMenu} from "../../redux/actions/actions";

const NavigationElement = ({item, focusTag, type}) => {
    const [openChild, setOpenChild] = useState(false)
    const dispatch = useDispatch()
    const setItemInList = (path, list) => {
        console.log(list)
        for (var item of list) {
            console.log(item)
            if (item.path === path) {
                return true
            }
        }
        return false
    }
    useEffect(() => {
        if (type === 1 && setItemInList(focusTag, item.list_items)) {
            setOpenChild(true)
        } else if (type === 0 && item.path === focusTag) {
            setOpenChild(true)
        } else {
            setOpenChild(false)
        }
    }, [focusTag, type])
    const handleCloseMobile = () => {
        document.querySelector("body").style.overflow = 'visible';
        dispatch(closeHomeMenu())
    }
    return (
        <li className='navigation-left-element'>
            <div className='navigation-left-element-title'>
                <LinkTemplate path={`/p/${item.path}`}>
                        <span className='navigation-left-p-title' onClick={handleCloseMobile}
                              style={{fontWeight: type === 0 && focusTag === item.path ? 'bold' : 'normal'}}>{item.name}</span>
                </LinkTemplate>
                <div style={{flexGrow: 1}}></div>
                {
                    item.list_items.length === 0 ? null : <div onClick={(e) => {
                        e.stopPropagation()
                        setOpenChild(!openChild)
                    }} className={openChild ? 'navigation-left-icon child-open' : 'navigation-left-icon'}>
                        <i className="fas fa-caret-down"></i>
                    </div>
                }
            </div>
            {
                item.list_items.length !== 0 ?
                    <ul className={openChild ? 'navigation-left-element-child-ul element-child-open' : 'navigation-left-element-child-ul'}>
                        {
                            item.list_items.map((item, index) => {
                                return <li className='navigation-left-element-child' key={index}>
                                    <LinkTemplate path={`/c/${item.path}`}>
                                <span className='navigation-left-p-title' onClick={handleCloseMobile}
                                      style={{fontWeight: focusTag === item.path && type === 1 ? 'bold' : 'normal'}}>{`${item.name}`}</span>
                                    </LinkTemplate>
                                </li>
                            })
                        }
                    </ul> : null
            }
        </li>
    );
};

export default NavigationElement;