import React, {useEffect} from "react";
import Header from "../../MainComponent/header/Header";
import Footer from "../../MainComponent/footer/Footer";
import "./AppTemplate.css";
import ScrollToTop from "../../RouteScrollToTop";
import Main from "../../MainComponent/main/Main";
import ProductDialog from "../../MainComponent/Dialog/ProductDialog";
import CartDialog from "../../MainComponent/Dialog/CartDialog";
import CheckoutDialog from "../../MainComponent/Dialog/CheckoutDialog";

export default function AppTemplate({children}) {
    return (
        <div className="AppTemplate-container">
            <ScrollToTop/>
            <Header/>
            <Main/>
            <Footer/>
            <ProductDialog/>
            <CartDialog/>
            <CheckoutDialog/>
        </div>
    );
}
