import React, {useEffect, useState} from 'react';
import './ImageComponent.css'
import DetailImage from "./DetailImage";

const ImageComponent = ({data}) => {
    const list_image = []
    for (const [, value] of Object.entries(data)) {
        if (value !== null) {
            list_image.push(value)
        }
    }
    const [urlImageMain, setUrlImageMain] = useState(data.url_image_main)
    useEffect(() => {
        setUrlImageMain(data.url_image_main)
    }, [data.url_image_main])
    const handleImageMainChange = (url) => {
        setUrlImageMain(url)
    }
    return (
        <div className={'image-component-container'}>
                        <div className={'image-component-main-container'}
                             style={{backgroundImage: `url(${urlImageMain})`}}>
                            <p className={'inner-text'}>Image main</p>
                        </div>
            <DetailImage data={list_image} changeImageMain={handleImageMainChange}/>
        </div>
    );
};

export default ImageComponent;