import React, {useEffect, useState} from 'react';
import './Main.css'
import {useDispatch} from "react-redux";
import {prefetchDataHotProductRequested} from "../../redux/actions/actions";
import Routes from "../../containers/routes/Routes";
import Messenger from '../../assets/messenger.png'
import IconZalo from '../../assets/icon-zalo.png'
import PhoneIcon from '../../assets/phone-icon.png'
import XIcon from '../../assets/x-icon.png'
const Main = () => {
    const dispatch = useDispatch()
    const [openContact, setOpenContact] = useState(false)
    useEffect(() => {
        dispatch(prefetchDataHotProductRequested())
    }, [])
    return (
        <main className="main-container">
            <div className="main-container-wrapper">
                <Routes/>
                <div className='main-icon-desktop'>
                    {
                        openContact?
                        <div className='main-icon-wrapper'>
                            <a className={'main-contact-element element-1'} href={'https://m.me/donghoplaza'} target={'_blank'}>
                                <img src={Messenger} alt={'messenger'} className='main-icon-other'/>
                                <p className='main-icon-title'>Liên hệ qua facebook</p>
                            </a>
                            <a className={'main-contact-element element-2'} href={"http://zalo.me/0974491688"} target={'_blank'}>
                                <img src={IconZalo} alt={'messenger'} className='main-icon-other'/>
                                <p className='main-icon-title'>Liên hệ qua Zalo</p>
                            </a>
                            <a className={'main-contact-element element-3'} href={'tel:+84974491688'} target={'_blank'}>
                                <img src={PhoneIcon} alt={'messenger'} className='main-icon-other'/>
                                <p className='main-icon-title'>Gọi điện thoại tới chúng tôi</p>
                            </a>
                        </div> :null
                    }
                    <div className='main-contact' onClick={()=>{setOpenContact(!openContact)}}>
                        <img src={openContact?XIcon : PhoneIcon} alt='message' className='main-phone-icon'></img>
                    </div>
                </div>
            </div>
        </main>
    );
};

export default Main;