import React from 'react';
import SkeletonTemplate from "../template/Skeleton/SkeletonTemplate";
import './ImageComponentSkeleton.css'
const ImageComponentSkeleton = () => {
    return (
        <div className={"image-component-container"}>
            <div className={'skeleton-image-container'}>
                <div className={'skeleton-image-element'}>
                    <SkeletonTemplate width={'100%'} height={'100%'} animation={true}/>
                </div>
            </div>
            <div className={'skeleton-image-child-container'}>
                <div className = {'skeleton-image-child-element'}>
                    <SkeletonTemplate width="100%" height="100%" animation={true}/>
                </div>
            </div>
        </div>
    );
};

export default ImageComponentSkeleton;