import React, {useState, useRef} from "react";
import "./SearchBar.css";
import SearchHint from "./SearchHint";
import {connect} from "react-redux";
import {searchDataRequested} from "../../../redux/actions/actions";
import {useHistory} from "react-router-dom";

function SearchBar(props) {
    const [query, setQuery] = useState("");
    const [showHint, setShowHint] = useState(false);
    const typingTimeOut = useRef(null);
    const history = useHistory();

    function handleOnChange(event) {
        const value = event.target.value;
        setQuery(value);
        if (typingTimeOut.current) {
            clearTimeout(typingTimeOut.current);
        }
        typingTimeOut.current = setTimeout(() => {
            if (value) getHint(value);
        }, 500);
    }

    function getHint(queryTerm) {
        props.dispatch(
            searchDataRequested(
                `/all_product?query=${queryTerm}&page=1&item_per_page=8&order=is_hot,desc`
            )
        );
    }

    function handleKeySubmit() {
        history.push(`/search?query=${query}&page=1`);
        setQuery("");
    }

    return (
        <div className="searchbar-container">
            <input
                list="search-list"
                type="text"
                placeholder="Bạn tìm gì..."
                className="searchbar-input"
                value={query}
                onChange={handleOnChange}
                onFocus={() => setShowHint(true)}
                onBlur={() => setShowHint(false)}
                onKeyDown={(e) => {
                    if (e.key === "Enter" && query.length !== 0) handleKeySubmit();
                }}
            />
            <div
                className="searchbar-icon"
                onClick={() => {
                    if (query.length !== 0) handleKeySubmit();
                }}
            >
                <i className="fas fa-search"></i>
            </div>
            {query ? (
                <div className="searchbar-delete-icon" onClick={() => setQuery("")}>
                    <i className="fas fa-backspace"></i>
                </div>
            ) : null}
            <SearchHint query={query} showHint={showHint}/>
        </div>
    );
}

export default connect()(SearchBar);
