import React, {useEffect} from "react";
import "./Header.css";
import {prefetchDataLocationRequested, prefetchDataNavRequested, removeFromCard} from "../../redux/actions/actions";
import LinkTemplate from "../../containers/template/LinkTemplate";
import CartComponent from "./components/CartComponent";
import LocationComponent from "./components/LocationComponent";
import Toolbar from "./components/Toolbar";
import {useDispatch} from "react-redux";
import BodyTemplate from "../../containers/template/BodyTemplate";
import Logo from "../../assets/Logo.png"
function Header() {
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(prefetchDataNavRequested())
    }, [])
    return (
        <header>
            <section className="header-container">
                <div className="header-inner-level-0">
                    <BodyTemplate>
                        <div className="header-wrapper">
                            <LinkTemplate
                                path="/"
                            >
                                <img src = {Logo} alt={"Logo"} className='header-logo'/>
                            </LinkTemplate>
                            <div style={{flexGrow: 1}}>
                                <LocationComponent/>
                            </div>
                            <CartComponent/>
                        </div>
                    </BodyTemplate>
                </div>
            </section>
            <section className='header-toolbar-container'>
                <div className='header-toolbar-wrapper'>
                    <Toolbar/>
                </div>
            </section>
        </header>
    );
}

export default Header;
