import React from "react";
import {useHistory, useLocation} from "react-router-dom";
import LinkTemplate from "../../../containers/template/LinkTemplate";

function NavigationList({list}) {
    return (
        <div className="navigation-nav-list">
            {list.map((item, index) => (
                <LinkTemplate
                    path={`/c/${item.path}`} key = {index}
                >
                    <div className="navigation-nav-list-1" key={index}>
                        {item.name}
                    </div>
                </LinkTemplate>
            ))}
        </div>
    );
}

export default NavigationList;
