import React from 'react';
import './CheckoutDialog.css'
import SuccessIcon from "../../assets/success.svg";
import Dialog from "../../containers/template/Dialog";
import {useDispatch, useSelector} from "react-redux";
import {closeCheckoutDialog, removeAllCart} from "../../redux/actions/actions";
import {useHistory} from 'react-router-dom'
const CheckoutDialog = () => {
    const state_dialog = useSelector(state=>state.dialogReducer.checkoutDialog)
    const dispatch = useDispatch()
    const handleClickMore = ()=>{
        dispatch(closeCheckoutDialog())
        dispatch(removeAllCart())
        history.push('/')
    }
    const history = useHistory()
    return (
        <Dialog open={state_dialog.isOpen} onClose={handleClickMore}>
            <section className='checkout-dialog-container'>
                <img src={SuccessIcon} alt={'success'} className='checkout-dialog-image'/>
                <p className='checkout-dialog-title'>Đặt hàng thành công. Mã đơn hàng của bạn là <strong>{`#${state_dialog.data.order_id}`}</strong></p>
                <p className='checkout-dialog-title'>Chúng tôi sẽ liên hệ với bạn trong thời gian sớm nhất!</p>
                <button className='checkout-dialog-button' onClick = {handleClickMore}>Xem thêm sản phẩm khác</button>
            </section>
        </Dialog>
    );
};

export default CheckoutDialog;