import React from "react";
import "./CategoryTitle.css";
export default function CategoryTitle({name}) {
  return (
    <div className="categorytitle-container">
      <div className="categorytitle-menu-icon">
        <i className="fas fa-bars"></i>
      </div>
      <div className="categorytitle-title">{name}</div>
    </div>
  );
}
