import React from 'react';
import {useSelector} from "react-redux";
import './NavigationLeft.css'
import NavigationElement from "./NavigationElement";

const NavigationLeft = ({focusTag, type}) => {
    const navigation = useSelector(state => state.prefetchDataReducer.navigation)
    return (
        <ul className="navigation-left-container">
            {
                navigation.map((item, index) => {
                    return <NavigationElement key = {index} item={item} focusTag = {focusTag} type = {type}/>
                })
            }
        </ul>
    );
};

export default NavigationLeft;