import React from "react";
import "./SkeletonTemplate.css";

export default function SkeletonTemplate({
  animation = true,
  duration = "1.2s",
  count = 1,
  height = "10px",
  width = "100%",
  circle = false,
  backgroundColor = "#f1eff1",
  animationColor = "#f5f5f5",
  display = "block",
  extraStyle = {},
}) {
  const numberOfSkeleton = new Array(count < 1 ? 1 : count).fill(count);
  return numberOfSkeleton.map((item, index) => (
    <div
      key={index}
      style={{
        position: "relative",
        width: width,
        height: height,
        borderRadius: circle ? height : 0,
        backgroundColor: backgroundColor,
        overflow: "hidden",
        display: display,
        ...extraStyle,
      }}
    >
      {animation ? (
        <div
          className="SkeletonTemplate-animation"
          style={{
            backgroundColor: animationColor,
            position: "absolute",
            width: "5%",
            height: height,
            borderRadius: height,
            boxShadow: `0px 0px 60px 80px ${animationColor}`,
            animationDuration: duration,
            WebkitAnimationDuration: duration,
          }}
        ></div>
      ) : null}
    </div>
  ));
}