import React from 'react';
import './Toolbar.css'
import LinkTemplate from "../../../containers/template/LinkTemplate";
import CategoryTitle from "../navigation/CategoryTitle";
import Navigation from "../navigation/Navigation";
import {useSelector, useDispatch} from "react-redux";
import SearchBar from "./SearchBar";
import {closeHomeMenu, openHomeMenu} from "../../../redux/actions/actions";
import MobileMenu from "./MobileMenu";

const Toolbar = () => {
    const isOpen = useSelector(state => state.openMenuReducer)
    const isHoverAllowed = useSelector(state => state.hoverTitleReducer)
    const dispatch = useDispatch()
    return (
        <div className='toolbar-container'>
            <div className='navigation-wrapper'>
                <div
                    className="toolbar-icon-menu"
                    onClick={() =>
                        isOpen
                            ? dispatch(closeHomeMenu())
                            : dispatch(openHomeMenu())
                    }
                >
                    <i className="fas fa-bars"></i>
                </div>
                <MobileMenu open={isOpen}/>
                <div className='toolbar-title'>
                    <CategoryTitle name={'DANH MỤC SẢN PHẨM'}/>
                    {
                        isHoverAllowed ?
                            <div className='toolbar-navigation'>
                                <Navigation/>
                            </div> : null
                    }
                </div>
                <div className='toolbar-element-link'>
                    <LinkTemplate path='/'>
                        <div className={'toolbar-link'}>
                            TRANG CHỦ
                        </div>
                    </LinkTemplate>
                     <LinkTemplate path='/gioi-thieu'>
                        <div className={'toolbar-link'}>
                            GIỚI THIỆU
                        </div>
                    </LinkTemplate>
                    <LinkTemplate path='/tin-tuc'>
                        <div className={'toolbar-link'}>
                            TIN TỨC
                        </div>
                    </LinkTemplate>
                    <LinkTemplate path='/ho-tro'>
                        <div className={'toolbar-link'}>
                            HỖ TRỢ
                        </div>
                    </LinkTemplate>
                    <LinkTemplate path='/lien-he'>
                        <div className={'toolbar-link'}>
                            LIÊN HỆ
                        </div>
                    </LinkTemplate>
                </div>
                <div style={{flexGrow : 1}}></div>
                <div className='toolbar-search'>
                    <SearchBar/>
                </div>
            </div>
        </div>
    );
};

export default Toolbar;