import React, {useEffect} from 'react';
import './MobileMenu.css'
import {useDispatch} from "react-redux";
import {closeHomeMenu} from "../../../redux/actions/actions";
import ToolbarMobile from "./ToolbarMobile";
import Logo from '../../../assets/Logo.png'

const MobileMenu = ({open}) => {
    const dispatch = useDispatch()
    const handleClose = (e) => {
        e.preventDefault()
        document.querySelector("body").style.overflow = 'visible';
        dispatch(closeHomeMenu())
    }
    useEffect(() => {
        if (open === true) {
            document.querySelector("body").style.overflow = 'hidden';
        }
    }, [open])
    return (
        <React.Fragment>
            <div className={open ? 'mobile-menu-container open-container' : 'mobile-menu-container'}
                 onClick={handleClose}>
            </div>
            <div className={open ? 'mobile-menu-content open' : 'mobile-menu-content'} onClick={(e) => {
                e.stopPropagation()
            }}>
                <div onClick={handleClose} className='mobile-menu-back-icon'>
                    <i className="fas fa-arrow-left"></i>
                </div>
                <img src={Logo} className='mobile-menu-logo'/>
                <div className='mobile-menu-toolbar-container'>
                    <ToolbarMobile/>
                </div>
            </div>
        </React.Fragment>
    );
};

export default MobileMenu;