import React, {useEffect, useState} from 'react';
import './DetailHead.css'
import SaleProgram from "./DetailHead/SaleProgram";
import {useDispatch} from "react-redux";
import {addToCard, closeProductDialog, openCartDialog} from "../../../../redux/actions/actions";
import {useHistory} from "react-router-dom"
import LinkTemplate from "../../../template/LinkTemplate";

const DetailHead = ({data, isPreview}) => {
    const dispatch = useDispatch()
    const [amount, setAmount] = useState(1)
    const history = useHistory()
    const handleDecreaseAmount = () => {
        if (amount > 0) {
            setAmount(amount - 1)
        }
    }
    const handleIncreaseAmount = () => {
        setAmount(amount + 1)
    }
    const handleBuyNow = () => {
        let newAmount = 0;
        if (amount <= 0) {
            newAmount = 1
        } else {
            newAmount = amount
        }
        dispatch(addToCard({
            product_id: data.product_id,
            amount: newAmount,
            price: data.price,
            sale_price: data.sale_price,
            url_image_main: data.url_image_main,
            name: data.name
        }))
        dispatch(closeProductDialog())
        setAmount(0)
        history.push('/cart')
    }
    const handleAddToCart = () => {
        if (amount > 0) {
            dispatch(addToCard({
                product_id: data.product_id,
                amount: amount,
                price: data.price,
                sale_price: data.sale_price,
                url_image_main: data.url_image_main,
                name: data.name
            }))
            setAmount(1)
            dispatch(openCartDialog())
        }
    }
    useEffect(() => {
        setAmount(1)
    }, [data])
    return (
        <section className={'detail-head-container specific'}>
            <h1 className={'detail-head-title'}>
                {
                    isPreview ?
                        <LinkTemplate path={`/product/${data.product_id}`} >
                            <div onClick={()=>{dispatch(closeProductDialog())}} className='detail-head-link-product'>
                                {data.name}
                            </div>
                        </LinkTemplate> :
                        <span>{data.name}</span>
                }
            </h1>
            <div className='detail-head-remain'>
                Tình trạng : <span
                className='detail-head-remain-text'>{Number(data.remain) !== 0 ? 'Còn hàng' : 'Hết hàng'}</span>
            </div>
            <section className={'detail-head-price'}>
                <div className={'detail-head-saleContainer'}>
                    {
                        data.price !== data.sale_price && data.price !== -1 ?
                            <div className={'detail-head-salePrice'}>{`${Number(data.price).toLocaleString('en')}₫`}
                            </div> : null
                    }
                    {
                        data.price !== data.sale_price && data.price !== -1 ?
                            <div
                                className={'detail-head-salePercent'}>{`Giảm ${(100 - data.sale_price / data.price * 100).toFixed()} %`}
                            </div> : null
                    }
                </div>
                <strong
                    className={'detail-head-mainPrice'}>{data.price === -1 ? `Liên hệ` : `${Number(data.sale_price).toLocaleString('en')}₫`}</strong>
            </section>
            <hr className={'detail-head-divider'}/>
            <SaleProgram data={data.description_sale}/>
            <hr className={'detail-head-divider'} style={{marginTop: '1rem'}}/>
            {
                data.sale_price === -1 ? null :
                    <section>
                        <section className={'detail-head-amount'}>
                            <div className={'detail-head-amountTitle'}>Số lượng</div>
                            <button className='detail-head-button' onClick={handleDecreaseAmount}>
                                -
                            </button>
                            <input className={'detail-head-input'} type={'number'} value={amount}/>
                            <button className='detail-head-button' onClick={handleIncreaseAmount}>
                                +
                            </button>
                        </section>
                        <section className={'detail-head-cardContainer'}>
                            <button className="detail-head-btnCard add" onClick={handleAddToCart}>
                                <i className="fas fa-cart-plus" style={{marginRight: '0.4rem'}}></i>Thêm vào giỏ hàng
                            </button>
                            <button className="detail-head-btnCard now" onClick={handleBuyNow}>
                                Mua ngay
                            </button>
                        </section>
                    </section>
            }
            <div className={'detail-head-phone'}>
                Gọi <span className={'detail-head-phoneNumber'}>01234567890</span> để được tư vấn miễn phí
            </div>
        </section>
    );
};

export default DetailHead;