import {ActionType} from "../../constants/Constants";

export const openHomeMenu = () => ({
    type: ActionType.OPEN_HOME_MENU,
});
export const closeHomeMenu = () => ({
    type: ActionType.CLOSE_HOME_MENU,
});
export const searchDataRequested = (url) => ({
    type: ActionType.SEARCH_DATA_REQUESTED,
    url: url,
});
export const prefetchDataNavRequested = (data)=>({
    type : ActionType.PREFETCH_DATA_NAV_REQUESTED,
    data : data
})
export const prefetchDataLocationRequested = (data)=> ({
    type : ActionType.PREFETCH_DATA_LOCATION_REQUESTED,
    data:data
})
export const prefetchDataHotProductRequested = (data)=> ({
    type : ActionType.PREFETCH_DATA_HOT_PRODUCT_REQUESTED,
    data:data
})
export const addToCard = (data) => ({
    type: ActionType.ADD_TO_CARD,
    data: data
})

export const removeFromCard = (data) => ({
    type: ActionType.REMOVE_FROM_CARD,
    data: data
})

export const changeAmountCart = (data)=>({
    type : ActionType.CHANGE_VALUE_CART,
    data :data
})
export const allowTitleHover = (data)=>({
    type : ActionType.ALLOW_TITLE_HOVER,
    data:data
})

export const disAllowTitleHover = (data)=>({
    type : ActionType.DISALLOW_TITLE_HOVER,
    data:data
})
export const openProductDialog = (data) =>({
    type : ActionType.OPEN_PRODUCT_DIALOG,
    data: data
})

export const closeProductDialog = (data) => ({
    type : ActionType.CLOSE_PRODUCT_DIALOG,
    data : data
})

export const openCartDialog = (data) => ({
    type: ActionType.OPEN_CART_DIALOG,
    data : data
})

export const closeCartDialog = (data) => ({
    type: ActionType.CLOSE_CART_DIALOG,
    data : data
})

export const openCheckoutDialog = (data) => ({
    type: ActionType.OPEN_CHECKOUT_DIALOG,
    data : data
})
export const closeCheckoutDialog = (data) => ({
    type: ActionType.CLOSE_CHECKOUT_DIALOG,
    data : data
})

export const removeAllCart = ()=>({
    type : ActionType.REMOVE_ALL_CART
})