import React from "react";
import "./Middle.css";
import Approved from "../../../assets/approved.png";
import Quality from "../../../assets/best-quality.png";
import {useSelector} from "react-redux";

export default function Middle() {
    const location = useSelector(state => state.prefetchDataReducer.locations)
    return (
        <div className="Middle-container">
            <div className="Middle-headquarter-container">
                <div className="Middle-headaurter-title">
                    Trụ sở chính: {location.list_branch[0].address}
                </div>
                {
                    location.list_branch.map((item, index) =>
                        <div className="Middle-headquarter-side" key = {index}>
                            {item.name}: {" "}
                            <span className="Middle-headquarter-side-phone">{item.phone}</span>
                        </div>)
                }
                <div className="Middle-headquarter-side">
                    Góp ý khiếu nại:{" "}
                    <span className="Middle-headquarter-side-phone">{location.list_branch[0].phone}</span>
                </div>
            </div>
            <div className="Middle-connect-us-container">
                <div className="Middle-connect-us-icon-wrapper">
                    <div className="Middle-connect-us-icon-title">
                        Kết nối với chúng tôi
                    </div>
                    <a
                        href="https://www.facebook.com/"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="Middle-icon-container"
                    >
                        <i className="fab fa-facebook-square"></i>
                    </a>
                    <a
                        href="https://www.youtube.com/"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="Middle-icon-container"
                    >
                        <i className="fab fa-youtube-square"></i>
                    </a>
                    <a
                        href="https://shopee.vn/"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="Middle-icon-container"
                    >
                        <i className="fab fa-shopify"></i>
                    </a>
                </div>
                <div className="Middle-switchboard-container">
                    <div className="Middle-switchboard">
                        Tổng đài tư vấn và hỗ trợ khách hàng:{" "}
                        <span className="Middle-switchboard-phone">0999 888 999</span>
                    </div>
                    <div className="Middle-switchboard">
                        Email:{" "}
                        <span className="Middle-switchboard-phone">
              trungnguyengroup@gmail.com
            </span>
                    </div>
                    <div className="Middle-switchboard">
                        Hỗ trợ kỹ thuật:{" "}
                        <span className="Middle-switchboard-phone">0999 888 999</span>
                    </div>
                </div>
            </div>
            <div className="Middle-approve-container">
                <a
                    href="https://www.facebook.com/"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="Middle-icon-container"
                >
                    <img
                        className="Middle-approve-item"
                        src={Quality}
                        alt="chất lượng cao"
                    />
                </a>
                <a
                    href="https://www.facebook.com/"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="Middle-icon-container"
                >
                    <img
                        className="Middle-approve-item"
                        src={Approved}
                        alt="bộ công thương"
                    />
                </a>
            </div>
        </div>
    );
}
