import React, {useState} from 'react';
import Divider from "../../../containers/template/Divider";
import './ToolbarMobile.css'
import {useHistory} from 'react-router-dom'
import {useDispatch} from "react-redux";
import {closeHomeMenu} from "../../../redux/actions/actions";
import {useSelector} from "react-redux";
import NavigationLeft from "../../../containers/categories/NavigationLeft";

const ToolbarMobile = () => {
    const history = useHistory()
    const dispatch = useDispatch()
    const NaviData = useSelector(state => state.prefetchDataReducer.navigation)
    const handleClick = (e, path) => {
        history.push(path)
        document.querySelector("body").style.overflow = 'visible';
        dispatch(closeHomeMenu())
    }
    const [openProduct, setOpenProduct] = useState(false)
    return (
        <ul className='toolbar-mobile-container'>
            <li className='toolbar-mobile-element' onClick={(e) => {
                handleClick(e, '/')
            }}>
                Trang chủ
            </li>
            <li className='toolbar-mobile-element' onClick={(e) => {
                handleClick(e, '/gioi-thieu')
            }}>
                Giới thiệu
            </li>

            <li className='toolbar-mobile-product-list'>
                <div style={{display: 'flex', width: '270px', borderBottom: '1px solid rgba(0,0,0,0.1)'}}>
                    <div className='toolbar-mobile-element' onClick={() => {
                        setOpenProduct(!openProduct)
                    }}>Sản phẩm
                    </div>
                    <div style={{flexGrow: 1}}></div>
                    <div className='toolbar-mobile-icon' style={{transform: openProduct ? 'rotate(90deg)' : ''}}
                         onClick={() => {
                             setOpenProduct(!openProduct)
                         }}>
                        <i className="fas fa-chevron-right"></i>
                    </div>
                </div>
            </li>
            {
                <div
                    className={openProduct ? 'toolbar-mobile-product-container product-open' : 'toolbar-mobile-product-container'}>
                    <NavigationLeft/>
                </div>
            }
            <li className='toolbar-mobile-element' onClick={(e) => {
                handleClick(e, '/tin-tuc')
            }}>
                Tin tức
            </li>

            <li className='toolbar-mobile-element' onClick={(e) => {
                handleClick(e, '/ho-tro')
            }}>
                Hỗ trợ khách hàng
            </li>
            <li className='toolbar-mobile-element' onClick={(e) => {
                handleClick(e, '/lien-he')
            }}>
                Liên hệ
            </li>
        </ul>
    );
};

export default ToolbarMobile;