import React, {useEffect, useRef, useState} from "react";
import "./SlideTemplate.css";

export const SlideTemplate = ({data, isFixed, number_of_item, numMobile, numMedium, numDesktop}) => {
    const [numberOfShownItems, setNumberOfShownItems] = useState(() => {
        if (!isFixed) {
            if (window.matchMedia("(max-width: 768px)").matches) {
                return numMobile
            } else if (window.matchMedia("(max-width: 1280px)").matches) {
                return numMedium
            } else {
                return numDesktop
            }
        } else {
            return number_of_item
        }
    });

    const slider = useRef(null);
    const [isDisplayArrow, setDisplayArrow] = useState(false)
    const [releasing, setReleasing] = useState(false)
    const [startX, setStartX] = useState({
        isPressed: false,
        prevX: 0
    })
    const [current, setCurrent] = useState({
        pos: 0,
        item: 0
    })

    function handleLeftArrow() {
        let newItem = current.item + 1
        if (newItem > 0) {
            newItem = numberOfShownItems - data.length
        }
        let curPos = newItem * slider.current.offsetWidth / numberOfShownItems
        setCurrent({
            item: newItem,
            pos: curPos
        })
        setReleasing(true)
        setTimeout(() => {
            setReleasing(false)
        }, 250)
    }

    function handleRightArrow() {
        let newItem = current.item - 1
        if (newItem < numberOfShownItems - data.length) {
            newItem = 0
        }
        // console.log( slider.current.offsetWidth , numberOfShownItems)
        let curPos = newItem * slider.current.offsetWidth / numberOfShownItems
        setCurrent({
            item: newItem,
            pos: curPos
        })
        setReleasing(true)
        setTimeout(() => {
            setReleasing(false)
        }, 250)
    }

    useEffect(() => {
        if (!isFixed) {
            window.addEventListener("resize", handleOnResize);
        }

        function handleOnResize() {
            setCurrent({
                item: 0,
                pos: 0
            })
            var isTouch = ('ontouchstart' in document.documentElement);
            if (window.matchMedia("(max-width: 768px)").matches) {
                setDisplayArrow(false)
                setNumberOfShownItems(numMobile);
            } else if (window.matchMedia("(max-width: 1280px)").matches) {
                if (data.length > numberOfShownItems && !isTouch) {
                    setDisplayArrow(true)
                } else {
                    setDisplayArrow(false)
                }
                setNumberOfShownItems(numMedium);
            } else {
                if (data.length > numberOfShownItems && !isTouch) {
                    setDisplayArrow(true)
                } else {
                    setDisplayArrow(false)
                }
                setNumberOfShownItems(numDesktop);
            }
        }

        //resize window
        return () => {
            if (!isFixed) {
                window.removeEventListener("resize", handleOnResize);
            }
        }
    }, [data]);
    useEffect(() => {
        var isTouch = ('ontouchstart' in document.documentElement);
        if (isTouch) {
            setDisplayArrow(false)
            return
        }
        if (window.matchMedia("(max-width: 768px)").matches) {
            setDisplayArrow(false)
        } else if (window.matchMedia("(max-width: 1280px)").matches) {
            if (data.length > numberOfShownItems) {
                setDisplayArrow(true)
            } else {
                setDisplayArrow(false)
            }
        } else {
            if (data.length > numberOfShownItems) {
                setDisplayArrow(true)
            } else {
                setDisplayArrow(false)
            }
        }
    }, [data])
    const handleMouseDown = (e) => {
        let x = e.clientX
        e.preventDefault()
        setStartX({
            isPressed: true,
            prevX: x
        })
    }
    const handleMouseMove = (e) => {
        let x = e.clientX
        let pos = (x - startX.prevX)
        let slider_width = slider.current.offsetWidth
        let widthItem = slider_width / numberOfShownItems
        if (startX.isPressed) {
            let cur_pos = current.pos
            if (cur_pos >= 0) {
                cur_pos = cur_pos + pos / 3
            } else if (cur_pos <= slider_width - data.length * widthItem) {
                cur_pos = cur_pos + pos / 3
            } else {
                cur_pos = cur_pos + pos
            }
            setCurrent({
                ...current,
                pos: cur_pos
            })
        }
        setStartX({
            ...startX,
            prevX: x
        })
    }
    const handleMouseLeave = (e) => {
        if (startX.isPressed) {
            let slider_width = slider.current.offsetWidth
            let widthItem = slider_width / numberOfShownItems
            let curItem = Math.round(current.pos / (widthItem))
            if (numberOfShownItems > data.length) {
                curItem = 0
            } else {
                if (current.pos > 0) {
                    curItem = 0
                } else if (current.pos <= slider_width - data.length * widthItem) {
                    curItem = numberOfShownItems - data.length
                }
            }
            setCurrent({
                item: curItem,
                pos: widthItem * curItem
            })
            setStartX({
                ...current,
                isPressed: false,
            })
            setReleasing(true)
            setTimeout(() => {
                setReleasing(false)
            }, 250)
        }
    }
    return (
        <div className="SlideTemplate-container"
             style={{zIndex: startX.isPressed ? 99 : 1}}
             onMouseDown={handleMouseDown} onMouseMove={handleMouseMove}
             onMouseUp={handleMouseLeave} onMouseLeave={handleMouseLeave}
        >
            <div className="SlideTemplate-wrapper" ref={slider} style={{
                transform: `translate3d(${current.pos}px, 0px, 0px)`,
                transition: releasing ? "all 0.25s ease 0s" : "all 0s ease 0s",
            }}>
                {data.map((item, index) => (
                    <div
                        key={index}
                        className="SlideTemplate-item-container"
                        style={{width: `${100 / numberOfShownItems}%`}}
                    >
                        {item}
                    </div>
                ))}
            </div>
            {
                isDisplayArrow ?
                    <div
                        onClick={handleLeftArrow}
                        className="SlideTemplate-arrow-container slide-left-arrow"
                    >
                        <i className="fas fa-chevron-left"></i>
                    </div> : null
            }
            {
                isDisplayArrow ?
                    <div
                        onClick={handleRightArrow}
                        className="SlideTemplate-arrow-container slide-right-arrow"
                    >
                        <i className="fas fa-chevron-right"></i>
                    </div> : null
            }
        </div>
    );
};

