import React from 'react';
import LinkTemplate from "../../../containers/template/LinkTemplate";
import SaleItem from "../../../containers/template/SaleItem";
import {removeFromCard} from "../../../redux/actions/actions";
import {useDispatch, useSelector} from "react-redux";
import {useHistory} from 'react-router-dom'
import './CartComponent.css'
import CartIcon from '../../../assets/shopping-bag.svg'
const CartComponent = () => {
    const dispatch = useDispatch()
    const history = useHistory()
    const cartData = useSelector(state => state.cartReducer)
    const total = cartData.length === 0 ? 0 : cartData.reduce((sum, cur) => {
        return sum + cur.amount
    }, 0)
    return (
        <div className="header-cart-container">
            <div style={{display: "flex"}}>
                <div className="header-cart-icon-container">
                    <LinkTemplate path={'/cart'}>
                        <img src = {CartIcon} className='header-cart-icon'/>
                    </LinkTemplate>
                    {
                        total === 0 ? null :
                            <div className="header-cart-icon-number">
                                <span className="header-cart-number">{total}</span>
                            </div>
                    }
                </div>
                <div className='header-cart-text'>
                    <div style={{fontSize: '0.9em'}}>
                        Giỏ hàng
                    </div>
                    <div style={{fontWeight : 'bold', fontSize:'0.9em'}}>
                        {`(${total}) sản phẩm`}
                    </div>
                </div>
            </div>

            <div className='header-cart-list-cart'>
                {total == 0 ? <div>
                        <div className='header-cart-empty'>
                            <div className='header-cart-inner'>
                                123
                            </div>
                            <div className='header-cart-empty-title'>Không có sản phẩm nào</div>
                        </div>
                    </div> :
                    <div className='header-cart-wraper'>
                        <div className='header-cart-title'>
                            Sản phẩm mới thêm
                        </div>
                        <div className='header-cart-list-element'>
                            {
                                cartData.map((item, index) => {
                                    return <div key = {index}>
                                        <div className={'header-cart-element-wrapper'}>
                                            <SaleItem data={item}/>
                                            <div className={'header-cart-element-delete'}
                                                 onClick={() => dispatch(removeFromCard(
                                                     {
                                                         product_id: item.product_id
                                                     }
                                                 ))}>Xóa
                                            </div>
                                        </div>
                                        <hr className='header-cart-divider'></hr>
                                    </div>
                                })
                            }
                        </div>
                        <button className='header-cart-button-cart' onClick={() => history.push('/cart')}>
                            Đi tới giỏ hàng
                        </button>
                    </div>}
            </div>
        </div>
    );
};

export default CartComponent;