import React, {useEffect, useRef} from 'react';
import './Dialog.css'
import {Provider} from "react-redux";

const Dialog = ({open, onClose, children, width, height}) => {
    const ref = useRef(null)
    const handleClose = (e) => {
        e.preventDefault()
        document.querySelector("body").style.overflow = 'visible';
        onClose()
    }
    useEffect(() => {
        if (open === true) {
            document.querySelector("body").style.overflow = 'hidden';
        }
        return () => {
            document.querySelector("body").style.overflow = 'visible';
        }
    }, [open])
    return (
        <React.Fragment>
            {
                open ?
                        <div className='dialog-container' ref={ref} onClick={handleClose}>
                            <div className='dialog-content'  style={{width : width, height:height}} onClick={(e) => {
                                e.stopPropagation()
                            }}>
                                <button onClick={handleClose} className='dialog-close-button'>
                                    <i className="fas fa-times"></i>
                                </button>
                                {children}
                            </div>
                        </div> : null
            }
        </React.Fragment>
    );
};

export default Dialog;