import React from 'react';
import HeadProduct from "../../containers/product/Component/HeadProduct";
import Dialog from "../../containers/template/Dialog";
import {useSelector, useDispatch} from "react-redux";
import {closeProductDialog} from "../../redux/actions/actions";

const ProductDialog = () => {
    const dispatch = useDispatch()
    const state = useSelector(state => state.dialogReducer.dialog_product)
    return (
        <Dialog open={state.isOpen} onClose={() => dispatch(closeProductDialog())} width={'70%'} height={'80%'}>
                <HeadProduct data={{
                    images: state.product.images,
                    details: state.product.details
                }} isPreview={true}/>
        </Dialog>
    );
};

export default ProductDialog;