import React from 'react';
import './LocationComponent.css'

const LocationComponent = () => {
    return (
        <div className="header-locations-container">
            <div className="header-locations-location-wrapper">
                <div className="header-icon-container">
                    <i className="fas fa-map-marker-alt"></i>
                </div>
                <div className="header-locations-time-number">
                    <div className="header-locations-number">Địa chỉ</div>
                    <div className='header-location-hotline'>CT1, The Pride, Tố Hữu, La khê, Hà Đông, Hà Nội</div>
                </div>
            </div>
            <div className="header-locations-location-wrapper">
                <div className="header-icon-container">
                    <i className="fas fa-phone-volume"></i>
                </div>
                <div className="header-locations-time-number">
                    <div className="header-locations-number">Hotline</div>
                    <div className='header-location-hotline'> {`0974491688`}</div>
                    <div className='header-location-hotline'> {`0966503292`}</div>
                </div>
            </div>
        </div>
    );
};

export default LocationComponent;