import React from 'react';
import './HeadProduct.css'
import ImageComponent from "./HeadComponent/ImageComponent";
import DetailHead from "./HeadComponent/DetailHead";
import ImageComponentSkeleton from "../../skeleton/ImageComponentSkeleton";
import DetailHeadSkeleton from "../../skeleton/DetailHeadSkeleton";
const HeadProduct = ({data, isPreview, isLoading}) => {
    return (
        <section className='head-product-container pad'>
            {
                isLoading? <ImageComponentSkeleton/> :
                <ImageComponent data={data.images}/>
            }
            {
                isLoading? <DetailHeadSkeleton/> :
                <DetailHead data={data.details} isPreview={isPreview}/>
            }
        </section>
    );
};

export default HeadProduct;